







import Vue from 'vue'

import { ImpactType } from '../src/engine'

export default Vue.extend({
  name: 'Impacts',
  props: ['impacts'],
  methods: {
    icon(type: ImpactType) {
      switch(type) {
        case ImpactType.Blind:
          return 'blind'
        case ImpactType.Stressed:
          return 'weight'
        case ImpactType.Loaded:
          return 'weight'
        case ImpactType.Overloaded:
          return 'weight'
      }
    },
    title(type: ImpactType) {
      switch(type) {
        case ImpactType.Blind:
          return ''
        case ImpactType.Stressed:
          return ''
        case ImpactType.Loaded:
          return ''
        case ImpactType.Overloaded:
          return ''
      }
    }
  }
})
